import styles from '../ModerationQuestions/moderationQuestion.scss';

function ModerationQuestion({ data, isLoading }) {
	return /*html*/ `
    <div class="${styles.container}">
    ${isLoading ? `<div class="${styles.loading}">loading</div>` : ''}
      <h1>Danh Sach cac cu hoi c ong gop</h1>
      ${data
			.map((item) => {
				return `
          <div class="${styles.content}">
            <div class="${styles.item}">
              <div class="${styles.title}">Cu Hoi</div>
              <div>${item.question}</div>
            </div>  
            <div class="${styles.item}">
              <div class="${styles.title}">Cac ap an</div>
              <div>${item.answers
					.map((item) => {
						return `
                  <div class="${styles.answer}">
                    <div class="${styles.value}">${item.value}</div>
                    <div class="${styles.iscorrect}">${item.isCorrect}</div>
                  </div>
                `;
					})
					.join('')}</div>
              </div>  
              <div class="${styles.item}">
              <div class="${styles.title}">im</div>
              <div class="${styles.score}">${item.score}</div>
            </div>  
            <div class="${styles.item}">
              <div class="${styles.title}"> Kho</div>
              <div class="${styles.level}">${item.level}</div>
            </div>  
            <div class="${styles.item}">
              <div class="${styles.title}">Chc Nng</div>
              <div class="${styles.action}">
                <button data-id="${item.id}" type="button" class="btn btn-success ${styles.button}" data-button="add-question-${item.id}">Chon</button>
                <button data-id="${item.id}" type="button" class="btn btn-primary ${styles.button}" data-button="edit-question-${item.id}">Sa</button>
                <button data-id="${item.id}" type="button" class="btn btn-danger" data-button="delete-question-${item.id}">Xoa</button>
              </div>
            </div>  
          </div>
        `;
			})
			.join('')}
    </div>
  `;
}

export default ModerationQuestion;
